@for $row from 0 through 3 {
  @for $column from 0 through 3 {
    .position_#{$row}_#{$column}:not(.isMoving) {
      top: 110 * $row + 5px;
      left: 110 * $column + 5px;
    }
  }
}

@for $fromRow from 0 through 3 {
  @for $toRow from 0 through 3 {
    $name: row_from_#{$fromRow}_to_#{$toRow};

    @if $fromRow == $toRow {
      .#{$name} {
        top: 110 * $toRow + 5px;
      }
    } @else {
      .#{$name} {
        animation-duration: 0.2s;
        animation-name: $name;
        animation-fill-mode: forwards;
      }

      @keyframes #{$name} {
        from {
          top: 110 * $fromRow + 5px;
        }
        to {
          top: 110 * $toRow + 5px;
        }
      }
    }
  }
}

@for $fromColumn from 0 through 3 {
  @for $toColumn from 0 through 3 {
    $name: column_from_#{$fromColumn}_to_#{$toColumn};

    @if $fromColumn == $toColumn {
      .#{$name} {
        left: 110 * $toColumn + 5px;
      }
    } @else {
      .#{$name} {
        animation-duration: 0.2s;
        animation-name: $name;
        animation-fill-mode: forwards;
      }

      @keyframes #{$name} {
        from {
          left: 110 * $fromColumn + 5px;
        }
        to {
          left: 110 * $toColumn + 5px;
        }
      }
    }
  }
}


@media screen and (max-width: 400px) {
	@for $row from 0 through 3 {
		@for $column from 0 through 3 {
			.position_#{$row}_#{$column}:not(.isMoving) {
				top: 82 * $row + 5px;
				left: 82 * $column + 5px;
			}
		}
	}
	
	@for $fromRow from 0 through 3 {
		@for $toRow from 0 through 3 {
			$name: row_from_#{$fromRow}_to_#{$toRow};
	
			@if $fromRow == $toRow {
				.#{$name} {
					top: 82 * $toRow + 5px;
				}
			} @else {
				.#{$name} {
					animation-duration: 0.2s;
					animation-name: $name;
					animation-fill-mode: forwards;
				}
	
				@keyframes #{$name} {
					from {
						top: 82 * $fromRow + 5px;
					}
					to {
						top: 82 * $toRow + 5px;
					}
				}
			}
		}
	}
	
	@for $fromColumn from 0 through 3 {
		@for $toColumn from 0 through 3 {
			$name: column_from_#{$fromColumn}_to_#{$toColumn};
	
			@if $fromColumn == $toColumn {
				.#{$name} {
					left: 82 * $toColumn + 5px;
				}
			} @else {
				.#{$name} {
					animation-duration: 0.2s;
					animation-name: $name;
					animation-fill-mode: forwards;
				}
	
				@keyframes #{$name} {
					from {
						left: 82 * $fromColumn + 5px;
					}
					to {
						left: 82 * $toColumn + 5px;
					}
				}
			}
		}
	}
}